import enUS from 'antd/es/locale/en_US';
import zhCN from 'antd/es/locale/zh_CN';
import ptPT from 'antd/lib/locale/pt_PT';
import moment from 'moment';
import 'moment/locale/en-ca';
import 'moment/locale/pt';
import 'moment/locale/zh-cn';
import Dict from '@baifendian/adhere-util-dict';
export default {
    initStatic: function () {
        // 国际化
        Dict.handlers.Locals = function () { return ({
            zh_CN: 'zh_CN',
            pt_PT: 'pt_PT',
            en_US: 'en_US',
        }); };
        // antd的国际化资源
        Dict.handlers.LocalsAntd = function () { return ({
            zh_CN: zhCN,
            pt_PT: ptPT,
            en_US: enUS,
        }); };
        // moment国际化
        Dict.handlers.LocalsMoment = function () { return ({
            zh_CN: function () {
                moment.locale('zh-cn');
            },
            en_US: function () {
                moment.locale('en-ca');
            },
            pt_PT: function () {
                moment.locale('pt');
            },
        }); };
    },
    initRemote: function () { },
};
